// App.js
import React, { useState } from 'react';
import './App.css';
import Form from './components/Form';
import UserList from './components/UserList';
import Xuatsoluongkho from './components/Xuatsoluongkho';
import MyImage from './components/logotdtu.png';

//import GET from './components/GET';
function App() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [currentPage, setCurrentPage] = useState('form'); //  xc nh trang hin ti

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleMenuClick = (page) => {
    setCurrentPage(page);
    setIsMenuOpen(false); // ng menu khi chn mt trang
  };

  return (
    <div className="App">
      <header>
        <div className="menu-icon" onClick={toggleMenu}>
          <div className="menu-line" />
          <div className="menu-line" />
          <div className="menu-line" />
          <div className="menu-line" />
          <div className="menu-line" />
        </div>
        <h1 className="app-title">QUN L KHO HNG CA CASTVN</h1>
        <div className="image-container">
          <img src={MyImage} alt="Avatar" className="avatar-image" />
        </div>
      </header>
      <main>
        <div className={`form-container ${currentPage === 'form' ? 'visible' : 'hidden'}`}>
          <Form />
        </div>
        <div className={`user-list-container ${currentPage === 'userList' ? 'visible' : 'hidden'}`}>
          <UserList />
        </div>
        <div className={`chartmongo-container ${currentPage === 'userList2' ? 'visible' : 'hidden'}`}>
          <Xuatsoluongkho />
        </div>

        {currentPage === 'PAGE4' && (
          <div className="user-list-container">
            <Form />
          </div>
        )}
        {currentPage === 'CHUNG' && (
          <div className="user-list-container">
            <Form />
          </div>
        )}
      </main>
      <aside className={`side-menu ${isMenuOpen ? 'open' : ''}`}>
        <ul>
          <li onClick={() => handleMenuClick('form')}>NHP THNG TIN XUT NHP KHO</li>
          <li onClick={() => handleMenuClick('userList')}>LCH S NHP XUT</li>
          <li onClick={() => handleMenuClick('userList2')}>THNG K S LNG</li> {/* Thm mc mi */}
          <li onClick={() => handleMenuClick('PAGE4')}>CHA LM KP</li>
          <li onClick={() => handleMenuClick('CHUNG')}>CHA LM KP</li>
        </ul>
      </aside>
      
      {/* Thm footer vo y */}
      <footer className="footer">
        <p>Design by Thanh Huy V</p>
      </footer>
    </div>
  );
}

export default App;
