import React, { useState } from 'react';
import Form from '@rjsf/core';
import validator from '@rjsf/validator-ajv8';
import axios from 'axios';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css'; // Import CSS cho react-datepicker
import './UserLogin.css'; // Import file CSS ty chnh

const initialSchema = {
  title: 'NHP XUT HNG HA',
  type: 'object',
  required: ['item', 'action', 'quantity', 'reason', 'signer'],
  properties: {
    item: {
      type: 'string',
      title: 'Chn mt hng',
      enum: [
        'u d Spo2 (tr em)',
        'u d Spo2 (ngi ln)',
        'u d Spo2 (s sinh)',
        'Cp ni di Spo2',
        'Cm bin nhit  BT770',
        'Cm bin nhit  BT750',
        'Cm bin nhit  BT500 + 550',
        'Lc khng kh BT500',
        'Che mt dng 1 ln BT400',
        'UC BT300',
        'Bnh xe BT500',
        'Pin BT350 (chnh hng)',
        'UC BT350',
        'DOP 300',
        'DOP 350',
        'Np cao su UC',
        'Dy DOP BT350',
        'Dy UC 300',
        'Hp ng nng h BT500',
        'Jack s kin',
        'u d BT250',
      ],
    },
    action: {
      type: 'string',
      title: 'Nhp/Xut',
      enum: ['Nhp', 'Xut'],
    },
    quantity: { type: 'number', title: 'S lng' },
    reason: { type: 'string', title: 'Ghi l do nhp xut', format: 'textarea' },
    signer: {
      type: 'string',
      title: 'K tn',
      enum: ['Thanh Sn', 'Thanh Huy', 'Phi Hi'],
    },
  },
};

const InventoryForm = () => {
  const [formData, setFormData] = useState({});
  const [submitCount, setSubmitCount] = useState(0);
  const [selectedDate, setSelectedDate] = useState(new Date()); // State  lu tr ngy chn

  const handleSubmit = async ({ formData }) => {
    try {
      console.log('D liu gi i:', { ...formData, date: selectedDate });

      const response = await axios.post(
        'https://ap-southeast-1.aws.data.mongodb-api.com/app/data-tqlme/endpoint/NHAPXUATHANG',
        { ...formData, date: selectedDate } // Gi ngy  chn
      );

      console.log('Kt qu t server:', response.data);
      alert('D LIU  C LU LI CM N BN  S DNG');
      setFormData({}); // Reset form sau khi submit thnh cng
      setSelectedDate(new Date()); // Reset ngy  chn
      setSubmitCount(submitCount + 1);
    } catch (error) {
      console.error('Li khi gi d liu:', error);
    }
  };

  return (
    <div className="auth-form-container">
      <Form
        schema={initialSchema}
        validator={validator}
        formData={formData}
        onChange={({ formData }) => setFormData(formData)}
        onSubmit={handleSubmit}
      >
        <div>
          <label htmlFor="date">Chn ngy</label>
          <DatePicker
            selected={selectedDate}
            onChange={(date) => setSelectedDate(date)}
            dateFormat="dd/MM/yyyy" // nh dng ngy
            className="datepicker" // Thm class cho CSS ty chnh
          />
        </div>
        {/* Nt Submit */}
        <button type="submit" className="submit-button">
          Gi
        </button>
      </Form>
    </div>
  );
};

export default InventoryForm;
