import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './Xuatsoluongkho.css'; // Import CSS ty chnh

const UserList = () => {
  const [userData, setUserData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const fetchData = async () => {
    try {
      const response = await axios.get(
        'https://ap-southeast-1.aws.data.mongodb-api.com/app/data-tqlme/endpoint/LAYDULIEUSOLUONG'
      );
      setUserData(response.data);
      setError(null);
    } catch (error) {
      console.error('Li khi ly d liu:', error);
      setError('Khng th ti d liu. Vui lng th li sau.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const handleRefresh = () => {
    setLoading(true);
    fetchData();
  };

  return (
    <div className="container">
      <h2 className="title">DANH SCH NHP XUT HNG HA</h2>
      <div className="button-container">
        <button onClick={handleRefresh} disabled={loading} className="refresh-button">
          {loading ? 'Refreshing...' : 'Refresh'}
        </button>
      </div>
      {error && <p className="error-text">{error}</p>}
      <div className="data-table-container"> {/* Khung cun ngang */}
        <table className="data-table">
          <thead>
            <tr>
              <th>STT</th>
              <th>Mt hng</th>
              <th>S lng</th>
            </tr>
          </thead>
          <tbody>
            {userData.reduce((acc, item) => {
              // Ly tt c cc mt hng tr _id
              const quantities = Object.entries(item).filter(([key]) => key !== '_id');
              // Lu s lng mt hng cho mi item
              const itemCount = quantities.length;

              quantities.forEach(([key, value], index) => {
                acc.push({
                  stt: acc.length + 1, // S th t
                  name: key,           // Tn mt hng
                  quantity: value      // S lng
                });
              });

              return acc;
            }, []).map(({ stt, name, quantity }) => (
              <tr key={name}>
                <td>{stt}</td>       {/* Hin th s th t */}
                <td>{name}</td>      {/* Hin th tn mt hng */}
                <td>{quantity}</td>  {/* Hin th s lng */}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default UserList;
