import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './UserList.css'; // Import CSS ty chnh

const UserList = () => {
  const [userData, setUserData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [filterType, setFilterType] = useState(''); // Trng thi cho loi lc (nhp/xut)
  const [searchTerm, setSearchTerm] = useState(''); // Trng thi cho tn mt hng
  const [suggestions, setSuggestions] = useState([]); // Trng thi cho gi 

  const fetchData = async () => {
    try {
      const response = await axios.get(
        'https://ap-southeast-1.aws.data.mongodb-api.com/app/data-tqlme/endpoint/LICHSUNHAPXUAT'
      );
      setUserData(response.data);
      setError(null);
    } catch (error) {
      console.error('Li khi ly d liu:', error);
      setError('Khng th ti d liu. Vui lng th li sau.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const handleRefresh = () => {
    setLoading(true);
    fetchData();
  };

  const handleFilter = () => {
    // C th ty chnh thm logic lc  y nu cn
  };

  const filteredData = userData.filter(item => {
    // Lc theo tn mt hng
    const matchesSearchTerm = item.item.toLowerCase().includes(searchTerm.toLowerCase());

    // Lc theo loi nhp/xut nu c
    const matchesFilterType = filterType ? item.action === filterType : true;

    return matchesSearchTerm && matchesFilterType;
  });

  // To gi  da trn tn mt hng
  const handleSearchTermChange = (e) => {
    const value = e.target.value;
    setSearchTerm(value);

    if (value) {
      const filteredSuggestions = userData
        .map(item => item.item)
        .filter(itemName => itemName.toLowerCase().includes(value.toLowerCase()));
      setSuggestions(filteredSuggestions);
    } else {
      setSuggestions([]);
    }
  };

  const handleSuggestionClick = (suggestion) => {
    setSearchTerm(suggestion);
    setSuggestions([]); // n gi  sau khi chn
  };

  const handleResetFilters = () => {
    setFilterType('');
    setSearchTerm('');
    setSuggestions([]);
  };

  return (
    <div className="container">
      <h2 className="title">DANH SCH NHP XUT HNG HA</h2>
      <div className="button-container">
        <button onClick={handleRefresh} disabled={loading} className="refresh-button">
          {loading ? 'Refreshing...' : 'Refresh'}
        </button>
        <button onClick={handleResetFilters} className="reset-button">B lc</button> {/* Nt B lc */}
      </div>
      <div className="filter-container">
        <select onChange={(e) => setFilterType(e.target.value)} value={filterType}>
          <option value="">Tt c</option>
          <option value="Nhp">Nhp</option>
          <option value="Xut">Xut</option>
        </select>
        <input 
          type="text" 
          placeholder="Tm kim theo tn mt hng" 
          value={searchTerm} 
          onChange={handleSearchTermChange} 
        />
        {suggestions.length > 0 && (
          <ul className="suggestions-list">
            {suggestions.map((suggestion, index) => (
              <li key={index} onClick={() => handleSuggestionClick(suggestion)}>
                {suggestion}
              </li>
            ))}
          </ul>
        )}
        <button onClick={handleFilter} className="filter-button">Lc</button>
      </div>
      {error && <p className="error-text">{error}</p>}
      <div className="data-table-container"> {/* Khung cun ngang */}
        <table className="data-table">
          <thead>
            <tr>
              <th>STT</th>
              <th>Mt hng</th>
              <th>Hnh ng</th>
              <th>S lng</th>
              <th>L do</th>
              <th>Ngi k</th>
              <th>Ngy</th>
            </tr>
          </thead>
          <tbody>
            {filteredData.map((item, index) => (
              <tr key={item._id.$oid}>
                <td>{index + 1}</td>
                <td>{item.item}</td>
                <td>{item.action}</td>
                <td>{item.quantity}</td>
                <td>{item.reason}</td>
                <td>{item.signer}</td>
                <td>{new Date(item.date).toLocaleString('vi-VN')}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default UserList;
